@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");

@font-face {
  font-family: "RosesBolero";
  src:  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ url("./assets/font.ttf")
    format("truetype"); /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3â€”5 */
}

.App {
  text-align: center;
  background: url("./assets/bg.png");
  height: 100vh;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.video-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  transition: 0.3s ease-out;
  animation-delay: 0.3s;
}

video {
  object-fit: fill;
}

.video {
  width: 1396px;
  max-width: 90%;
  /* height: 785px; */
  max-height: 90%;
}

.play {
  position: absolute;
  cursor: pointer;
}

.leaves-container {
  position: absolute;
  height: 100vh;
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;

  background: url("./assets/leaves.png");
  background-size: cover;
  transition: 0.3s ease-in;
  pointer-events: none;
}

.movie-finished .leaves-container {
  opacity: 1;
}

.movie-finished .video-container {
  opacity: 0;
}

.invitation-container {
  position: absolute;
  width: 600px;
  max-width: 90%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in;
  transition-delay: 0.3s;
  opacity: 0;
  pointer-events: none;
}

.invite-intro {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 24px;
  color: #243221;

  width: 400px;
  max-width: 100%;
}

.invite-location {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 35px;
  color: #66754c;

  width: 500px;
  max-width: 100%;
  margin-top: 45px;
}

.invite-location a {
  color: #66754c;
}

.invite-rsvp {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 45px;
  line-height: 55px;
  color: #243221;
  margin-top: 0;
}

.invite-date {
  max-width: 90%;
}

.invite-cta {
  max-width: 90%;
  cursor: pointer;
}

.movie-finished .invitation-container {
  opacity: 1;
  pointer-events: auto;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(102, 117, 76, 0.8);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup {
  position: relative;
  font-family: "Inter", sans-serif;
  border-radius: 22px;
  background-color: #f9f6ef;
  padding: 64px 48px;
  width: 300px;
  max-width: 70%;
}

.popup-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;

  font-family: "Inter", sans-serif;
  font-weight: bold;
}

.popup-title {
  font-family: "Inter", sans-serif;
  font-weight: 500;

  font-size: 35px;
  color: #243221;
  margin-bottom: 18px;
}

.popup-form-row {
  display: flex;
  margin-bottom: 18px;
}

input,
textarea {
  padding: 8px;
  border-radius: 4px;
  border: none;
  border: 2px solid;
  border-color: #243221;
  width: 100%;
}

.popup-form-remove {
  padding: 3px 8px;
  cursor: pointer;
  margin-left: 8px;
  border: 2px solid #dcc460;
  border-radius: 4px;
  color: #dcc460;
  font-weight: 500;
}

.popup-form-add {
  padding: 3px 8px;
  cursor: pointer;
  /* border: 2px solid #dcc460; */
  border-radius: 4px;
  color: #fff;
  font-weight: normal;
  font-size: 24px;
  background-color: #66754c;
}

.popup-checkbox {
  font-weight: 500;
  align-self: flex-start;
  margin-top: 28px;
}

.popup-checkbox input {
  width: auto;
}

.popup-submit {
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 4px;
  color: #fff;
  /* font-weight: 500; */
  line-height: 28px;
  font-size: 32px;
  margin-top: 16px;
  background-color: #243221;
}

.details {
  z-index: 1;
  width: 450px;
  max-width: 90%;
  font-family: "Inter", sans-serif;
}

.countdown {
  font-size: 32px;
  font-weight: bold;
  color: #66754c;
  margin-bottom: 12px;
}

.countdown-timer {
  display: block;
  font-family: "RosesBolero";
  font-size: 52px;
  color: #000000;
  margin-bottom: 36px;
}

.program {
  width: 100%;
  display: flex;
  margin-bottom: 36px;
  justify-content: space-evenly;
  align-items: center;
}

.separator {
  display: inline-flex;
  width: 2px;
  height: 100px;
  background-color: #66754c;
}

.icon {
  height: 94px;
}

.program-title {
  font-size: 24px;
  font-weight: bold;
  color: #243221;
}

.program-text {
  font-size: 20px;
  font-weight: 500;
  color: #66754c;
}

.description {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #243221;
  margin-bottom: 16px;
}

.description b {
  /* font-size: 24px; */
}
.description-2 {
  color: #66754c;
}

.description a {
  color: #66754c;
  cursor: pointer;
}

.description a img {
  cursor: pointer;
  /* display: block; */
  margin-top: 16px;
}

@media only screen and (max-width: 740px) {
  .movie-finished {
    justify-content: flex-end;
    height: calc(100vh - 20px);
    padding-bottom: 20px;
  }

  .invite-intro {
    font-size: 20px;
    line-height: 28px;
  }

  .invite-date {
    max-width: 80%;
  }

  .invite-location {
    font-size: 16px;
    line-height: 24px;
  }

  .invite-rsvp {
    font-size: 24px;
    line-height: 32px;
  }

  .invite-cta {
    max-width: 60%;
  }

  .countdown {
    font-size: 24px;
  }

  .countdown-timer {
    font-size: 28px;
  }

  .program {
    justify-content: space-evenly;
  }

  .icon {
    height: 48px;
  }

  .program-title {
    font-size: 18px;
  }

  .description {
    font-size: 14px;
    line-height: 22px;
  }

  .location {
    max-width: 64px;
  }
}

@media only screen and (min-width: 740px) and (max-width: 1440px) {
  .movie-finished {
    /* justify-content: flex-end;
    height: calc(100vh - 20px);
    padding-bottom: 20px; */
  }

  .invite-intro {
    font-size: 20px;
    line-height: 28px;
  }

  .invite-date {
    max-width: 80%;
  }

  .invite-location {
    font-size: 16px;
    line-height: 24px;
  }

  .invite-rsvp {
    font-size: 24px;
    line-height: 32px;
  }

  .invite-cta {
    max-width: 200px;
  }

  .countdown {
    font-size: 24px;
  }

  .countdown-timer {
    font-size: 28px;
  }

  .program {
    justify-content: space-evenly;
  }

  .icon {
    height: 64px;
  }

  .program-title {
    font-size: 18px;
  }

  .description {
    font-size: 14px;
    line-height: 22px;
  }

  .location {
    max-width: 64px;
  }
}
